import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SideNavComponent } from './side-nav/side-nav.component';
import { AuthGuard } from './gaurds/auth.guard';

const routes: Routes = [
  {
    path: '',
    redirectTo: '/dashboard',
    pathMatch: 'full'
  },
  {
    path: 'login',
    loadChildren: () => import('./login/login.module').then( m => m.LoginModule),
  },
  {
    path: 'dashboard',
    component: SideNavComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: 'view/:',
        loadChildren: () => import('./main-view/main-view.module').then( m => m.MainViewModule),
      },
    ]
  },

  {
    path: 'view-all-process',
    canActivate: [AuthGuard],
    loadChildren: () => import('./process-flow-editor/view-all-process/process-flow.module').then( m => m.ProcessFlowModule)
    // component: GeneralProcessComponent
  },
  {
    path: 'create-view/:',
    canActivate: [AuthGuard],
    loadChildren: () => import('./create-view/create-view.module').then( m => m.CreateViewModule)
    // component: GeneralProcessComponent
  },
  {
    path: 'edit-view/:',
    canActivate: [AuthGuard],
    loadChildren: () => import('./edit-view/edit-view.module').then( m => m.EditViewModule)
    // component: GeneralProcessComponent
  },
  {
    path: 'edit-process',
    redirectTo: 'view-all-process',
    pathMatch: 'full'
  },
  {
    path: 'edit-process/:',
    canActivate: [AuthGuard],
    loadChildren: () => import('./process-flow-editor/process-dashboard/process-dashboard.module').then( m => m.ProcessDashboardModule )
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
